        <MenuItem name="companyInfo">
          <Icon type="ios-paper" />
        </MenuItem>
        <Submenu name="talentStatusNei">
          <template slot="title">
            <Icon type="ios-people" />
          </template>
          <MenuItem name="talentStatusNei">--</MenuItem>
          <MenuItem name="talentStatusWai">--</MenuItem>
        </Submenu>
        <Submenu name="flowInNei">
          <template slot="title">
            <Icon type="ios-stats" />
          </template>
          <MenuGroup title="">
            <MenuItem name="flowInNei">--</MenuItem>
            <MenuItem name="flowInWai">--</MenuItem>
          </MenuGroup>
          <MenuGroup title="">
            <MenuItem name="flowOutNei">--</MenuItem>
            <MenuItem name="flowOutWai">--</MenuItem>
          </MenuGroup>
          <MenuGroup title="">
            <MenuItem name="flowTalentInfoNei">--</MenuItem>
            <MenuItem name="flowTalentInfoWai">--</MenuItem>
          </MenuGroup>
        </Submenu>
        <Submenu name="needNei">
