            <Button class="button" type="info" @click="faqDownload"></Button>
          </div>
          <div class="submit">
            <Button class="button" :loading="saveLoading" type="primary" @click="saveHandle"></Button>
          </div>
          <div class="submit">
            <Button type="success" @click="submitHandle" :loading="submitLoading"></Button>
          </div>
          <div class="submit">
            <Button
              type="warning"
              icon="md-arrow-down"
