      axios({
        url: url.getForm,
        method: "get",
        params: {
          userId
        }
      }).then(res => {
        let { form } = res.data;

        if (form && form._confirmed) {
          _this.$Message.error("");
          this.saveLoading = false;
        } else {
          let clearedForm = clearForm(_this.$store.state.form);
          // --**--
          return axios({
            url: url.save,
            method: "post",
            data: {
              form: clearedForm,
              userId: _this.$store.state.form._from_user
            }
          })
            .then(res => {
              this.saveLoading = false;
              this.$message({
                message: "",
                type: "success",
                duration: 1000
              });
            })
            .catch(err => {
              console.log(err);
              this.saveLoading = false;
            });
        }
      });
    },
