      let verifyMsg = null,
        _this = this,
        userId = this.$store.state.form._from_user;
      this.submitLoading = true;
      axios({
        url: url.getForm,
        method: "get",
        params: {
          userId
        }
      }).then(res => {
        let { form } = res.data;

        if (form && form._confirmed) {
          _this.submitLoading = false;
          _this.$Message.error("");
          return;
        }

        if (!_this.$store.state.form._basic[4]) {
          _this.submitLoading = false;
          _this.$message.error("    ");
          return;
        }

        verifyMsg = verify(_this.$store.state.form);

        if (!verifyMsg.verify) {
          _this.$Message.error({
            content: verifyMsg.msg,
            duration: 10,
            closable: true
          });
          _this.submitLoading = false;
          return;
        }

        _this
          .$confirm("  , ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
          .then(() => {

            let clearedForm = clearForm(_this.$store.state.form);
            // 
            // --**--
            axios({
              url: url.submit,
              method: "post",
              data: { form: clearedForm }
            }).then(res => {
              _this.$message({
                type: "success",
                message: "!"
              });
              _this.submitLoading = false;
            });
          })
          .catch(() => {
            _this.submitLoading = false;
          });
      });
