<template>
  <div class="container">
    <div class="box">
      <div class="account">
        <div class="account-label"></div>
        <div class="account-input">
          <Input v-model="account" type="text" placeholder="" style="width: 230px" />
        </div>
      </div>
      <div class="account">
        <div class="account-label"></div>
        <div class="account-input">
          <Input v-model="idNum" type="text" placeholder="" style="width: 230px" />
        </div>
      </div>
      <div class="account">
        <div class="account-label"> &nbsp;&nbsp; </div>
        <div class="account-input">
          <Input v-model="password1" type="password" placeholder="" style="width: 230px" />
        </div>
      </div>
      <div class="account">
        <div class="account-label"></div>
        <div class="account-input">
          <Input v-model="password2" type="password" placeholder="" style="width: 230px" />
        </div>
      </div>
      <div class="button">
        <Button type="primary" shape="circle" long @click="registerHandle"></Button>
      </div>
      <div class="forget">
        <router-link to="/login"></router-link>
      </div>
    </div>
  </div>
</template>
<style scoped>
.forget {
  text-align: right;
  width: 300px;
  margin: 0 auto;
}
.account {
  display: flex;
  margin: 20px auto;
  width: 500px;
}
.account-label {
  flex: 1;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.account-input {
  flex: 2;
}
.button {
  width: 300px;
  margin: 75px auto 20px;
}

.container {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "", Arial, sans-serif;
}
.box {
  width: 670px;
  height: 350px;
  margin: 100px auto;
}
</style>
<script>
import url from "@/service.config.js";
import axios from "axios";
import util from "@/utils.js";

export default {
  data() {
    return {
      account: "",
      password1: "",
      password2: "",
      idNum: "",
      modal1: false
    };
  },
  methods: {
    registerHandle() {
      console.log(this.$data.password1.length);
      const patt_id = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      // this.modal1 = true;
      if (this.$data.account.length < 6) {
        this.$message.error("6");
        return;
      }
      if (!patt_id.test(this.$data.idNum)) {
        this.$message.error("");
        return;
      }
      if (this.$data.password1.length < 6) {
        this.$message.error("6");
        return;
      }
      if (this.$data.password1 !== this.$data.password2) {
        this.$message.error("");
        return;
      }
      let title = "";
      axios({
        url: url.reg,
        method: "get",
        params: {
          user_name: this.$data.account,
          user_password: this.$data.password1,
          user_id_card: this.$data.idNum
        }
      })
        .then(res => {
          console.log(res.data);
          if (res.data === -1) {
            this.$message.error("");
            return;
          }
          if (res.data === -2) {
            this.$message.error("");
            return;
          }
          this.$Modal.success({
            title: title,
            okText: "",
            onOk: () => {
              this.$router.push("/login");
            }
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>