import { basicVerify } from './basic';
import { summaryVerify } from './summary';
import { flowInVerify } from './flow-in';
import { flowOutVerify } from './flow-out';
export const verify = form => {
  // 
  let verifyMsg = {
    verify: true,
    msg: ''
  },
    basicResult = null,
    summaryResult = null,
    flowInResult = null,
    flowOutResult = null;
  // 
  basicResult = basicVerify(form._basic);
  if (!basicResult.verify) {
    verifyMsg.msg = ` ${basicResult.label} ${basicResult.msg}`;
    verifyMsg.verify = false;
    return verifyMsg;
  }

  // 
  switch (form._basic[4].value) {
    case '':
      // 
      summaryResult = summaryVerify(form._summary);

      if (!summaryResult.verify) {
        verifyMsg.msg = `${summaryResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      // 
      flowInResult = flowInVerify(form._sum_in);

      if (!flowInResult.verify) {
        verifyMsg.msg = `${flowInResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      // 
      flowOutResult = flowOutVerify(form._sum_out);

      if (!flowOutResult.verify) {
        verifyMsg.msg = `${flowOutResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }

      break;
    case '':
      // 
      summaryResult = summaryVerify(form._summary);

      if (!summaryResult.verify) {
        verifyMsg.msg = `${summaryResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      // 
      flowInResult = flowInVerify(form._sum_in);

      if (!flowInResult.verify) {
        verifyMsg.msg = `${flowInResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }

      // 
      flowOutResult = flowOutVerify(form._sum_out);

      if (!flowOutResult.verify) {
        verifyMsg.msg = `${flowOutResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      break;
    case '':
      // 
      summaryResult = summaryVerify(form._summary_nei);

      if (!summaryResult.verify) {
        verifyMsg.msg = `-${summaryResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      // 
      summaryResult = summaryVerify(form._summary_wai, false);

      if (!summaryResult.verify) {
        verifyMsg.msg = `-${summaryResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      // 
      flowInResult = flowInVerify(form._sum_in_nei);

      if (!flowInResult.verify) {
        verifyMsg.msg = `-${flowInResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      // 
      flowInResult = flowInVerify(form._sum_in_wai, false);

      if (!flowInResult.verify) {
        verifyMsg.msg = `-${flowInResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }

      // 
      flowOutResult = flowOutVerify(form._sum_out_nei);

      if (!flowOutResult.verify) {
        verifyMsg.msg = `-${flowOutResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }

      // 
      flowOutResult = flowOutVerify(form._sum_out_wai, false);

      if (!flowOutResult.verify) {
        verifyMsg.msg = `-${flowOutResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
      break;
    default:
      // 
      summaryResult = summaryVerify(form._summary);

      if (!summaryResult.verify) {
        verifyMsg.msg = `${summaryResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }

      // 
      flowInResult = flowInVerify(form._sum_in);

      if (!flowInResult.verify) {
        verifyMsg.msg = `${flowInResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }

      // 
      flowOutResult = flowOutVerify(form._sum_out);

      if (!flowOutResult.verify) {
        verifyMsg.msg = `${flowOutResult.msg}`;
        verifyMsg.verify = false;
        return verifyMsg;
      }
  }



  // 
  return verifyMsg;
}