<template>
  <div class="box">
    <Row>
      <i-col span="4">
        <Alert type="warning" show-icon>
          
          <template slot="desc"></template>
        </Alert>
      </i-col>
    </Row>
    <el-tabs tab-position="left" :value="activeYear" @tab-click="handleClick">
      <el-tab-pane v-for="(value, key) in summaryOutter" :key="key" :label="value.year" :name="value.year">
        <div v-if="activeYear === value.year">
          <p class="title">-<span class="special"></span>--<span class="special"></span>-{{value.year}}</p>
          <TalentStatus
            :key="`${key}`"
            :getSummaryObjCombin="getSummaryWaiObjCombin(value.year)"
            :summaryInput="summaryInput(value.year)"
            :twoLevelStationCategory="twoLevelSummaryWaiStationCategory(value.year)"
            :year="value.year"
            :summary="value.info"
            commitFunction="setSummeryOut"
          />
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<style scoped>
.special {
  color: #ed4014;
  font-weight: bold;
  font-size: 24px;
}
.box {
  margin-top: 20px;
}

.title {
  text-align: center;
  font-size: 16px;
  font-weight: bolder;
}
</style>
<script>
import TalentStatus from "../components/TalentStatus.vue";

export default {
  components: {
    TalentStatus
  },
  data() {
    return {
      activeYear: 2018
    }
  },
  methods: {
    handleClick(tab, e) {
      this.$data.activeYear = tab.name;
    },
  },
  computed: {
    summaryOutter() {
      return this.$store.state.form._summary_wai;
    },

    getSummaryWaiObjCombin() {
      return year => {
        return this.$store.getters.getSummaryWaiObjCombin(year);
      };
    },

    summaryInput() {
      return year => {
        return this.$store.getters.getSummaryWaiInput(year);
      };
    },

    twoLevelSummaryWaiStationCategory() {
      return year => {
        return this.$store.getters.twoLevelSummaryWaiStationCategory(year);
      };
    }
  }
};
</script>
