import { objectHelper } from '../../util/object-helper';
import {
  suoShuDiYu,
  industryCategory,
  danWeiXingZhi,
  xingBie,
  nianLingJieGou,
  xueLiJieGou,
  xinChou,
  major,
  liZhiYuanYin,
  renCaiCengCi
} from '../../assets/category/category';
function initEveryYear(begin, end, data) {
  let dataArr = [];
  for (let i = begin; i >= end; i--) {
    dataArr.push({
      "year": i,
      "info": objectHelper.deepCopy(data)
    });
  }
  return dataArr;
}

