export const summaryVerify = (_summary, isMust = true) => {
	// Array
	_summary = Array.from(_summary);
	// 
	let verifyMsg = {
			verify: true,
			msg: '',
			label: ''
		},
		haveDataYearIndexArr = [];

	// index
	_summary.forEach((item, index) => {
		if (item.info[0].value > 0) {
			haveDataYearIndexArr.push(index);
		}
	});

	if (isMust) {
		if (_summary[0].info[0].value === 0) {
			verifyMsg.verify = false;
			verifyMsg.msg = '2018';

			return verifyMsg;
		}

		// 
		if (haveDataYearIndexArr.length === 0) {
			verifyMsg.verify = false;
			verifyMsg.msg = '';

			return verifyMsg;
		}
	}
	
	for (let index of haveDataYearIndexArr) {
		// 
		let yearSum = 0;
		for (let eachClass of _summary[index].info[0].children.inputChildren) {
			yearSum += eachClass.value;
		}

		// 
		if (_summary[index].info[0].value !== yearSum) {
			verifyMsg.verify = false;
			verifyMsg.msg = `${_summary[index].year}`;

			return verifyMsg;
		}

		// 
		for (let eachClassStructure of _summary[index].info[0].children.children) {
			let classObj = _summary[index].info[0].children.inputChildren.find((value) => {
				return value.prop === eachClassStructure.prop;
			});

			for (let eachStructureObj of eachClassStructure.children) {
				// 
				if (eachStructureObj.label === '') {
					let structureNum = 0;

					for (let selectSpecial of eachStructureObj.value) {
						if (selectSpecial.num && selectSpecial.cas.length === 0) {
							verifyMsg.verify = false;
							verifyMsg.msg = `${_summary[index]
								.year} ${classObj.label}  ${eachStructureObj.label} `;

							return verifyMsg;
						}
						if (selectSpecial.num) {
							structureNum += selectSpecial.num;
						}
					}

					if (classObj.value !== structureNum) {
						verifyMsg.verify = false;
						verifyMsg.msg = `${_summary[index]
							.year} ${classObj.label}  ${eachStructureObj.label} `;

						return verifyMsg;
					}
				} else {
					let eachStructureArr = eachStructureObj.children,
						structureNum = 0;

					eachStructureArr.forEach((structure) => {
						structureNum += structure.value;
					});

					if (classObj.value !== structureNum) {
						verifyMsg.verify = false;
						verifyMsg.msg = `${_summary[index]
							.year} ${classObj.label}  ${eachStructureObj.label} `;

						return verifyMsg;
					}
				}
			}
		}
	}

	return verifyMsg;
};
